$(document).ready(function() {
	$('#header').load('header.html');
	$('#footer').load('footer.html');

  	$(window).on('scroll',function(event) {
        var scroll = $(window).scrollTop();
        if (scroll < 245) {
            $(".navbar").removeClass("sticky");
        } else {
            $(".navbar").addClass("sticky");
        }
    });

    $('.product-slider--implant').slick({
        slidesToShow: 4,
        nav: true,
        // variableWidth: true,
        // centerMode: true,
        appendArrows: '.product-slider-nav--implant-manufacturing',
        prevArrow: "<span><img src='public/img/icons/left-chevron.svg' alt='Left Arrow'></span>",
        nextArrow: "<span><img src='public/img/icons/right-chevron.svg' alt='Right Arrow'></span>",

        responsive: [
            {
                breakpoint: 1920,
                settings: {
                    slidesToShow: 3
                }
            },
            {
                breakpoint: 1280,
                settings: {
                    slidesToShow: 2
                }
            },
            {
                breakpoint: 500,
                settings: {
                    slidesToShow: 1
                }
            }
        ]
    });

    $('.product-slider--instrument').slick({
        slidesToShow: 4,
        nav: true,
        // variableWidth: true,
        // centerMode: true,
        appendArrows: '.product-slider-nav--instrument-manufacturing',
        prevArrow: "<span><img src='public/img/icons/left-chevron.svg' alt='Left Arrow'></span>",
        nextArrow: "<span><img src='public/img/icons/right-chevron.svg' alt='Right Arrow'></span>",
        responsive: [
            {
                breakpoint: 1920,
                settings: {
                    slidesToShow: 3
                }
            },
            {
                breakpoint: 1280,
                settings: {
                    slidesToShow: 2
                }
            },
            {
                breakpoint: 500,
                settings: {
                    slidesToShow: 1
                }
            }
        ]
    });

    $('.hero-slider-wrapper').slick({
        slidesToShow: 4,
        arrows: false,
        infinite: false,

        responsive: [
            {
                breakpoint: 1279,
                settings: {
                    slidesToShow: 3,
                    variableWidth: true
                }
            }
        ]
    });

    var $slider = $('.quality-control-slider-wrapper');

    if ($slider.length) {
        var currentSlide;
        var slidesCount;
        var sliderCounter = document.createElement('div');
        sliderCounter.classList.add('slider-counter');
      
        var updateSliderCounter = function(slick, currentIndex) {
            currentSlide = slick.slickCurrentSlide() + 1;
            slidesCount = slick.slideCount;
            $(sliderCounter).text(currentSlide + '/' +slidesCount)
        };

        $slider.on('init', function(event, slick) {
            $('.quality-control-slider--nav').append(sliderCounter);
            updateSliderCounter(slick);
        });

        $slider.on('afterChange', function(event, slick, currentSlide) {
            updateSliderCounter(slick, currentSlide);
        });

        $slider.slick({
            slidesToShow: 1,
            nav: true,
            infinite: false,
            appendArrows: '.quality-control-slider--nav',
            prevArrow: "<span><img src='public/img/icons/left-chevron-black.svg' alt='Left Arrow'></span>",
            nextArrow: "<span><img src='public/img/icons/right-chevron-black.svg' alt='Right Arrow'></span>"
        });
    }

    var historySliderSettings = {
        slidesToShow: 1,
        dots: true,
        customPaging: function(slider, i) { 
            return '<button>' + $(slider.$slides[i]).attr('title') + '</button>';
        },
        infinite: false,
        nav: true,
        appendArrows: '.history-slider--nav',
        prevArrow: "<span><img src='public/img/icons/left-chevron-black.svg' alt='Left Arrow'></span>",
        nextArrow: "<span><img src='public/img/icons/right-chevron-black.svg' alt='Right Arrow'></span>",
        
        responsive: [
            {
                breakpoint: 1023,
                settings: "unslick"
            }
        ]
    };

    var historySlider =  $('.history-slider').slick(historySliderSettings);
          
    $(window).on('resize', function() {
        if( $(window).width() > 1023 &&  !historySlider.hasClass('slick-initialized')) {
            $('.history-slider').slick(historySliderSettings);
        }
    });
});